/* RestaurantList.module.css */

.container {
  margin: 0;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.title {
  font-size: 20px;
  font-weight: 700;
  color: #1f2937;
}

.filterButtons {
  display: flex;
  gap: 8px;
}

.filterButton {
  padding: 4px 12px;
  font-size: 14px;
  border: 1px solid #d1d5db;
  border-radius: 9999px;
  background-color: white;
  color: #374151;
  transition: background-color 0.2s;
}

.filterButton:hover {
  background-color: #f9fafb;
}

.grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 24px;
}

@media (min-width: 768px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.restaurantCard {
  background-color: white;
  border-radius: 8px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
  transition: box-shadow 0.2s;
}

.restaurantCard:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.cardContent {
  padding: 24px;
}

.restaurantHeader {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
}

.restaurantImage {
  font-size: 30px;
}

.restaurantInfo h3 {
  font-weight: 600;
  color: #1f2937;
}

.restaurantInfo p {
  font-size: 14px;
  color: #6b7280;
}

.restaurantMeta {
  display: flex;
  align-items: center;
  gap: 16px;
  font-size: 14px;
  color: #6b7280;
  margin-bottom: 16px;
}

.restaurantDetails {
  font-size: 14px;
  color: #6b7280;
  margin-bottom: 16px;
}

.restaurantDetails p {
  margin: 0;
}

.orderButton {
  width: 100%;
  background-color: #2563eb;
  color: white;
  padding: 8px;
  border-radius: 8px;
  font-weight: 500;
  transition: background-color 0.2s;
  border: none;
  cursor: pointer;
}

.orderButton:hover {
  background-color: #1d4ed8;
}